// import Button from '@restart/ui/esm/Button'
import React, { useContext, useState } from 'react'
import { Form, Button } from 'react-bootstrap'
import { EmployeeContext } from '../context/EmployeeContext'




export const AddForm = () => {
    const { addEmployee } = useContext(EmployeeContext)

    const [newEmployee, setNewEmployee] = useState({
        name: "",
        email: "",
        address: "",
        phone: "",
    })

    const handleChange = (e) => {
        let { name, value } = e.target
        setNewEmployee({ ...newEmployee, [name]: value })
    }

    const { name, email, address, phone } = newEmployee;

    const handleSubmit = (e) => {
        e.preventDefault()
        addEmployee(name, email, address, phone);
    }


    return (
        <>
            <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3">
                    <Form.Control type="text" placeholder="Name" required name="name" value={name} onChange={(e) => handleChange(e)} />
                </Form.Group>
                <Form.Group className="mb-3">
                    <Form.Control type="email" placeholder="Email" required name="email" value={email} onChange={(e) => handleChange(e)} />
                </Form.Group>
                <Form.Group className="mb-3">
                    <Form.Control as="textarea" placeholder="Address" rows={3} name="address" value={address} onChange={(e) => handleChange(e)} />
                </Form.Group>
                <Form.Group className="mb-3">
                    <Form.Control type="text" placeholder="Phone" name="phone" value={phone} onChange={(e) => handleChange(e)} />
                </Form.Group>

                <div className="d-grid gap-2 mb-3">
                    <Button variant="success" type="submit" block>
                        Add Employee
                    </Button>
                </div>


            </Form>
        </>
    )
}
