// import Button from '@restart/ui/esm/Button'
import React, { useContext, useState, useEffect } from 'react'
// import { Button } from 'react-bootstrap'
import { EmployeeContext } from '../context/EmployeeContext'
import { Modal, Button } from 'react-bootstrap'
import EditForm from './EditForm'


const Employee = ({ employee }) => {
    const { deleteEmploye } = useContext(EmployeeContext)
    const [show, setShow] = useState(false)

    const handleShow = () => {
        setShow(true)
    }
    const handleClose = () => {
        setShow(false)
    }

    useEffect(() => {
        handleClose()
    }, [employee])


    return (
        <>
            <td>{employee.name}</td>
            <td>{employee.email}</td>
            <td>{employee.address}</td>
            <td>{employee.phone}</td>
            <td>
                <button onClick={handleShow} className="btn text-warning btn-act" data-toggle="modal"><i className="material-icons" data-toggle="tooltip" title="Edit">&#xE254;</i></button>
                <button onClick={() => deleteEmploye(employee.id)} className="btn text-danger btn-act" data-toggle="modal"><i className="material-icons" data-toggle="tooltip" title="Delete">&#xE872;</i></button>
            </td>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>
                        Add Employee
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <EditForm employee={employee} />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="success" onClick={handleClose}>
                        Close Button
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default Employee
